import React, { useState } from "react"

const App = () => {
  const [name, setName] = useState("")
  const handleEdit = (e) => {
    setName(e.target.value)
  }
  return (
    <div>
      <h1 style={{textAlign: 'center'}} >Welcome</h1>

      <div style={{
        display: 'flex',
        justifyContent: 'center',
        margin: '50px'

      }} >
        <input style={{ width: '300px', padding: '10px 20px' }} placeholder="Enter any thing..." value={name} onChange={handleEdit} />
      </div>
        <br />
        <div style={{
        display: 'flex',
        justifyContent: 'center',
      }} 
      >
        <h3>{name}</h3>

      </div>
    </div>
  )
}

export default App